import React, { createContext, useContext, useState, useEffect } from 'react';
import { WebSocketMessage } from '../services/WebSocket';
import webSocketService from '../services/WebSocket';

interface ProfileContextData {
  accountData: WebSocketMessage['accountData'] | null;
  updateAccountData: (data: WebSocketMessage['accountData']) => void;
}

const ProfileContext = createContext<ProfileContextData>({
  accountData: null,
  updateAccountData: () => {},
});

export const useProfile = () => useContext(ProfileContext);

export const ProfileProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [accountData, setAccountData] = useState<WebSocketMessage['accountData'] | null>(null);

  useEffect(() => {
    //    WebSocket
    const handleConnect = (data: WebSocketMessage) => {
      console.log('ProfileContext:    WebSocket:', data);
      if (data.type === 'connect' && data.status === 'success' && data.accountData) {
        console.log('ProfileContext:    :', data.accountData);
        setAccountData(data.accountData);
      }
    };

    //  
    webSocketService.addMessageHandler('connect', handleConnect);

    //    
    return () => {
      webSocketService.removeMessageHandler('connect');
    };
  }, []);

  const updateAccountData = (data: WebSocketMessage['accountData']) => {
    console.log('ProfileContext:    :', data);
    setAccountData(data);
  };

  return (
    <ProfileContext.Provider value={{ accountData, updateAccountData }}>
      {children}
    </ProfileContext.Provider>
  );
}; 